@import url(https://fonts.googleapis.com/css2?family=Chakra+Petch&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;font-family:'Chakra Petch', sans-serif}#root{background-color:#eee}.App{width:600px;min-height:100vh;padding:20px 10px;margin:auto;display:grid;grid-template-rows:80px auto;grid-template-columns:1fr 1fr;justify-items:center;background-color:#fff}button{padding:.3em 1em;border:1px solid #ddd;border-radius:4px;font-size:14px}button:not(.activated){background-color:#ddd;color:#aaa}button.activated:hover{cursor:pointer}button.activated:active{scale:1.1;background-color:#eee}ul{margin-top:0px;margin-bottom:0px;padding-left:0}ul li{list-style:none}p{margin-top:2px;margin-bottom:2px}.no-display{display:none !important}.hidden{visibility:hidden}.span-columns{grid-column:1 / -1}.activated{background-color:#fff;color:#000}.shot,.hit{transition-property:background-color;transition-duration:1s;transition-timing-function:ease-out}.shot{background-color:#afd2fa}#other-board .cell:not(.shot):not(.hit):hover{cursor:pointer;box-shadow:0 0 3px #faa inset;position:relative}#other-board .cell:not(.shot):not(.hit):hover:before{content:'';position:absolute;height:25px;border-left:1px solid #faa;left:10.5px;box-shadow:0 0 1px #faa,0 0 1px #faa inset}#other-board .cell:not(.shot):not(.hit):hover:after{content:'';position:absolute;width:25px;border-top:1px solid #faa;top:10.5px;box-shadow:0 0 1px #faa,0 0 1px #faa inset}.part>span,.cell>span{display:block;width:80%;height:80%;border-radius:50%}.minesweeper span{background-color:#888}.submarine span{background-color:#999}.cruiser span{background-color:#aaa}.battleship span{background-color:#bbb}.carrier span{background-color:#ccc}#board .cell.hit span{background-color:#faa}#other-board .cell.hit span{background-color:#faa}#notifications{width:100%;padding:5px;display:grid;grid-template-columns:1fr 1fr;justify-content:center;align-content:center;border:3px ridge #ccc;font-size:20px;text-align:center}#notifications p:not(.span-columns):last-child{border-left:2px solid #ccc}.board h3{text-align:center}.board>ul{width:254px;display:grid;grid-template-columns:repeat(10, 25px);grid-template-rows:repeat(10, 25px);border:2px solid #ccc;border-radius:4px}.board .cell{border-bottom:1px solid #ddd;border-right:1px solid #ddd;font-size:12px;text-transform:capitalize;list-style:none;display:grid;justify-items:center;align-items:center}.board .cell:nth-child(10n){border-right:none}.board .cell:nth-child(n+91){border-bottom:none}.board .setup:hover{cursor:pointer}#setup{margin-top:20px;text-align:center}#setup p{text-align:left}.shipyard{height:125px;display:grid;grid-template-columns:repeat(10, 25px);grid-template-rows:repeat(5, 25px);grid-auto-flow:column dense}.ship{display:grid;grid-template-columns:repeat(10, 25px);grid-template-rows:repeat(5, 25px)}.ship li{display:grid;justify-items:center;align-items:center}.ship:hover{cursor:pointer}

